window.addEventListener('DOMContentLoaded', () => {
  const socialFixed = document.querySelector('.socialFixed');
  const socialFixedContainer = document.querySelector(
    '.socialFixed .socialFixed__container',
  );
  const socialFixedClosed = socialFixed.querySelector('.close');

  if (socialFixedClosed) {
    socialFixedClosed.addEventListener('click', () => {
      socialFixedClosed.classList.toggle('show');
      socialFixedContainer.classList.toggle('show');
    });
  }
});
